<template>
  <div class="order-wrap">
    <h3>您的产品</h3>
    <div class="order-list-choose">
      <div class="order-list-option">
        选择产品：
        <v-selection :selections="products" @on-change="productChange"></v-selection>
      </div>

      <div class="order-list-option">
        开始日期：
        <v-date-picker @on-change="getStartDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        结束日期：
        <v-date-picker @on-change="getEndDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        关键词：
        <input type="text" v-model.lazy="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="head in tableHeads" @click="changeOrderType(head)" :class="{active:head.active}">{{ head.label }}</th>
        </tr>
        <tr v-for="item in tableData" :key="item.orderNo">
          <!--<td v-for="head in tableHeads">{{ item[head.key] }}</td>-->
          <td>{{ item.orderNo }}</td>
          <td>{{ item.product }}</td>
          <td>{{ item.buyTypeName }}</td>
          <td>{{ item.versionName }}</td>
          <td>{{ item.periodName }}</td>
          <td>{{ item.date }}</td>
          <td>{{ item.buyNumber }}</td>
          <td>{{ item.price }}</td>
          <td>{{ item.orderStatus }}</td>
          <td v-if="item.orderStatus == 1">
            <div class="delete" @click="deleteOrder(item.id)" >
              删除
            </div>
            <div class="delete" >
              修改
            </div>
          </td>
          <td v-else-if="item.orderStatus ==0">
            <span>增加</span><span>查询</span>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import VSelection from '../components/base/selection'
import VDatePicker from '../components/base/datepicker'
import _ from 'lodash'
export default {
  components: {
    VSelection,
    VDatePicker
  },
  data () {
    return {
      query: '',
      productId: 0,
      startDate: '',
      endDate: '',
      products: [
        {
          label: '数据统计',
          value: 0
        },
        {
          label: '数据预测',
          value: 1
        },
        {
          label: '流量分析',
          value: 2
        },
        {
          label: '广告发布',
          value: 3
        }
      ],
      tableHeads: [
        {
          label: '订单号',
          key: 'orderNo'
        },
        {
          label: '购买产品',
          key: 'product'
        },
        {
          label: '产品类型',
          key: 'buyTypeName'
        },
        {
          label: '版本类型',
          key: 'versionName'
        },
        {
          label: '有效时间',
          key: 'periodName'
        },
        {
          label: '购买日期',
          key: 'date'
        },
        {
          label: '数量',
          key: 'buyNumber'
        },
        {
          label: '总价',
          key: 'price'
        },
        {
          label: '状态',
          key: 'orderStatus'
        },
        {
          label: '操作',
          key: 'orderStatus'
        }
      ],
      currentOrder: 'asc',
      tableData: []
    }
  },
  watch: {
    query () {
      this.getList()
    }
  },
  methods: {
    deleteOrder( id){
      console.log(id);
      axios.get("/api/delete", {
        params: { 'id':id }
      }).then(function (response) {
          this.getList()
      }).catch(function (error) {
        alert(error);
      });
    },
    productChange (obj) {
      this.productId = obj.value
      this.getList()
    },
    getStartDate (date) {
      this.startDate = date
      this.getList()
    },
    getEndDate (date) {
      this.endDate = date
      this.getList()
    },
    getList () {
      console.log("getList...")
      let reqParams = {
        query: this.query,
        productId: this.productId,
        startDate: this.startDate,
        endDate: this.endDate
      }
//      axios.post('test/getOrderList', reqParams)
      axios.post('/api/getOrderList', reqParams)
        .then((res) => {
        this.tableData = res.data.list
      }, (err) => {

      })

  },
    changeOrderType (headItem) {
      this.tableHeads.map((item) => {
        item.active = false
        return item
      })
      headItem.active = true
      if (this.currentOrder === 'asc') {
        this.currentOrder = 'desc'
      }
      else if (this.currentOrder === 'desc') {
        this.currentOrder = 'asc'
      }
      this.tableData = _.orderBy(this.tableData, headItem.key, this.currentOrder)
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

<style scoped>
  .delete {
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 5px 10px;
  }
.order-wrap {
  width: 1200px;
  min-height: 800px;
  margin: 20px auto;
  overflow: hidden;
}
.order-wrap h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-option {
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child {
  padding-left: 0;
}
.order-list-table {
  margin-top: 20px;
}
.order-list-table table {
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}
</style>



